*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

.main-div{
    width: 100%;
    height: 100vh;
    background-color: #5d8be2;
    display: flex;
    flex-direction: row;
    justify-content:center;
    align-items: center;
    text-align: center;
}

.center-div{
    position: relative;
    width: 350px;
    height: 80%;
    background-color: aliceblue;
    box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
    border-radius: 15px;
    
}

h1{
    margin: 20px 0;
    background-color: rebeccapurple;
    color: white;
    padding: 5px;
    box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.3);
}

input{
    text-align: center;
    height: 30px;
    width: 60%;
    outline: none;
    border: none;
    background: transparent;
    font-size: 20px;
    font-weight: 500;
    border-bottom: 2px solid rebeccapurple;
}

.addBtn{
    min-height: 40px;
    width: 40px;
    color: white;
    background-color: rebeccapurple;
    border-radius: 50%;
    font-size: 25px;
    font-weight: 500;
    margin-left: 10px;
    box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.3);
    outline: none;
    border: none;
}

.addBtn:hover{
    background-color: #26982e;
    cursor: pointer;   
    transition: 0.3s;
}
ol{
    margin-top: 30px;
    max-height: 60%;
    overflow-y: scroll;
}

::-webkit-scrollbar{
    width: 5px;
}

::-webkit-scrollbar-track{
    background: #f1f1f1;
}

::-webkit-scrollbar-thumb{
    background: #888;
}

ol li{
    list-style: none;
    text-align: left;
    color: #8a53c1;
    font-size: 20px;
    font-weight: 550;
}

.itemDiv{
    margin: 10px 0;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.fa{
    margin: 5px 10px;
    padding: 4px;
    background-color: #743fa8;
    color: white;
    border-radius: 50%;
    transform: scale(0.7);
    box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.3);
}
.fa:hover{
    background-color: #be1414;
    cursor: pointer;
}

@media(max-width: 421px){
    .center-div{
        width:300px;
    }
    
}

@media(max-width: 327px){
    .center-div{
        width:250px;
        height: 70%;
    }
    ol{
        max-height: 50%;
    }
    h1{
        font-size: 25px;
    }
}

.copyDiv{
    position: absolute;
    bottom: 14px;
    right: 20px;
    transform: scale(0.8);
    font-weight: 700;
    letter-spacing: 2px;
    cursor: pointer;
    
}



